.index {
    display: flex;
    :global {
        .archive-type-list {
            width: 120px;
            padding: 20px 0px;

            .type-item {
                display: flex;
                width: 100%;
                line-height: 38px;

                .type-title {
                    width: 100%;
                    font-size: 12px;
                    border: solid 1px var(--main-border-color);
                    padding: 0px 12px;
                    text-align: center;
                    line-height: 22px;
                    border-radius: 8px;
                }
                .type-count {
                    width: 20px;
                    font-size: 14px;
                    text-align: right;
                    position: relative;
                    right: 22px;
                    top: -7px;
                }
            }

        }
        .archive-list {
            border-left: solid 1px var(--main-border-color);
            width: calc(100% - 100px);
            padding: 8px 0px 8px 8px;
            .archive-item {
                display: flex;
                line-height: 40px;

                &:hover {
                    border-bottom: solid 2px var(--main-active-color);
                }

                .type {
                    width: 100px;
                    .type-content {
                        font-size: 12px;
                        border: solid 1px var(--main-border-color);
                        padding: 0px 12px;
                        text-align: center;
                        line-height: 22px;
                        border-radius: 8px;
                        margin-top: 10px;
                    }
                }
                .title {
                    margin-left: 10px;
                    font-size: 16px;
                    width: calc(100% - 100px);
                }
                .time {
                    font-size: 14px;
                    color: var(--main-secondary-text-color);
                    width: 100px;
                    text-align: right;
                }
            }

            .ant-empty {
                padding-top: 10px;
            }
        }
    }
}